$(document).ready(function() {
	var testimonialsManager = new TestimonialsManager($('#testimonial_manager'));
	testimonialsManager.init();
});

function TestimonialsManager(context) {
	var self = this,
		filters = context.closest('#testimonial_filters'),
		list = context.closest('#testimonial_list');
	
	this.init = function () {
		filters.on('click', 'a[role="tab"]', function() {
			var filter = $(this).data('status');
			var testimonials = list.children('.testimonial');
			if (filter === 1) {
				testimonials.each(function(testimonial) {
					if (testimonial.hasClass('panel-success')) {
						testimonial.removeClass('hidden');
					} else {
						testimonial.addClass('hidden');
					}
				});
			} else if (filter === 0) {
				testimonials.each(function(testimonial) {
					if (testimonial.hasClass('panel-danger')) {
						testimonial.removeClass('hidden');
					} else {
						testimonial.addClass('hidden');
					}
				});
			} else {
				testimonials.each(function(testimonial) {
					testimonial.removeClass('hidden');
				});
			}
		});
		
		list.on('click', '.activation-toggle', function () {
			var id = $(this).closest('.testimonial').attr('id');
			var testimonial_id = parseInt(id.substring(12), 10);
			console.log(testimonial_id);
			self.toggleActiveStatus(testimonial_id);
		});
	};
	
	this.toggleActiveStatus = function (testimonial_id) {
		$.ajax({
			url: '/admin/testimonials/' + testimonial_id + '/toggle-status',
			dataType: 'json',
			type: 'post',
			beforeSend: function() {
			},
			error: function(err, obj, msg) {
				console.log(msg);
			},
			success: function (response) {
				console.log(response.message);
			}
		});
	};
}
